import { Component, OnInit } from '@angular/core';
import { DragulaService } from 'ng2-dragula';
import { Observable } from 'rxjs/Rx';
import { Chart } from 'angular-highcharts';

@Component({
  selector: 'app-sales-dashboard',
  templateUrl: './sales-dashboard.component.html',
  styleUrls: ['./sales-dashboard.component.css']
})
export class SalesDashboardComponent implements OnInit {

  public widgetTiles: Array<string> = ['YTDSales', 'pieChart', 'lineChart', 'monthlySalesLineChart', 'salesGrowthLineChart'];
  public dashTiles: Array<string> = [];

  public dayTime: Date;

  showHide: boolean = false;

  public setZoomChart: string = null;

  options: any = {
    removeOnSpill: true
  }

  constructor(private dragulaService: DragulaService) {

    // call time
    this.setChangeTime();

    // set drag option 
    dragulaService.setOptions('dragTile', {
      removeOnSpill: false
    });


    dragulaService.dropModel.subscribe((value) => {
      this.onDropModel(value);
    });

  }


  remmoveTile(data) {
    var search_term = data;

    for (var i = this.dashTiles.length - 1; i >= 0; i--) {
      if (this.dashTiles[i] === search_term) {
        this.dashTiles.splice(i, 1);

      }
    }

    let dashTileData = JSON.stringify(this.dashTiles);
    localStorage.setItem("dashTiles", dashTileData);

    this.removeDuplicates(this.widgetTiles, this.dashTiles);
  }


// remove duplicates
  removeDuplicates(first, second) {
    for (var i = 0; i < second.length; i++) {
      var index = undefined;
      while ((index = first.indexOf(second[i])) !== -1) {
        first.splice(index, 1);
      }
    }
  }


  // time change method
  setChangeTime() {
    this.dayTime = new Date();

    Observable.interval(2000)
      .subscribe(response => {
        this.dayTime = new Date();
      }, error => {
      });

  }


  private onDropModel(value: any) {

    // save tiles to local storage
    let dashTileData = JSON.stringify(this.dashTiles);
    localStorage.setItem("dashTiles", dashTileData)


  }


  ngOnInit() {
// get saved tiles from local storage
    this.dashTiles = JSON.parse(localStorage.getItem("dashTiles"));

    function removeDuplicates(first, second) {
      for (var i = 0; i < second.length; i++) {
        var index = undefined;
        while ((index = first.indexOf(second[i])) !== -1) {
          first.splice(index, 1);
        }
      }
      return first;
    }

    // remofe if duplicates have
    this.widgetTiles = removeDuplicates(this.widgetTiles, this.dashTiles);

  }

  // distroy drag object when move to another route
  ngOnDestroy() {
    this.dragulaService.destroy('dragTile');
  }



  openCloseWidgetBar() {

    this.showHide = !this.showHide;
    if (this.showHide == false) {
      document.getElementById("widget-bar").style.right = "-43%";
    } else {
      document.getElementById("widget-bar").style.right = "0";
    }

  }



  getTileData(data) {
    this.setZoomChart = data;
  }



//  chart static data
  pieChart = new Chart({
    chart: {
      type: 'pie'
    },
    title: {
      text: ''
    },
    credits: {
      enabled: false
    },
    series: [
      {
        name: 'Pie 1',
        data: [
          {
            "name": "Sales 1",
            "y": 62,
            color: "#4472c4"
          },
          {
            "name": "Sales 2",
            "y": 25,
            color: "#ed7d31"
          },
          {
            "name": "Sales 3",
            "y": 13,
            color: "#a5a5a5"
          }

        ]
      }
    ],
    plotOptions: {
      series: {
        dataLabels: {
          enabled: false
        }
      }
    },
  });




  lineChart = new Chart({
    chart: {
      type: 'column'
    },
    legend: {
      enabled: false
    },
    title: {
      text: ''
    },
    xAxis: {
      categories: [
        '2012',
        '2013',
        '2014',
        '2015',
        '2016'
      ],
      crosshair: true,
    },
    colors: ['#4472c4', '#ed7d31'],
    yAxis: {
      min: 0,
      title: {
        text: ''
      }
    },
    series: [{
      name: 'Male',
      data: [130, 100, 150, 175, 200]

    }, {
      name: 'Female',
      data: [60, 25, 75, 125, 125]

    }],
    plotOptions: {
      series: {
        dataLabels: {
          enabled: false
        }
      }
    },
  });



  monthlySalesLineChart = new Chart({
    chart: {
      type: 'column'
    },
    legend: {
      enabled: false
    },
    title: {
      text: ''
    },
    xAxis: {
      categories: [
        'Jan',
        'Feb',
        'Mar',
        'Apr',
        'May',
        'Jun',
        'Jul',
        'Aug',
        'Sep',
        'Oct',
        'Nov',
        'Dec'
      ],
      crosshair: true,
    },
    colors: ['#4472c4'],
    yAxis: {
      min: 0,
      title: {
        text: ''
      },
    },
    series: [{
      name: '',
      data: [0.1, 0.5, 0.8, 0.4, 1, 0.5, 0.8, 0.1, 0.7, 0.4, 0.8, 0.3, 0.12]

    }],
    plotOptions: {
      series: {
        dataLabels: {
          enabled: false
        }
      }
    },
  });



  salesGrowthLineChart = new Chart({
    legend: {
      enabled: false
    },
    title: {
      text: ''
    },
    xAxis: {
      categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
    },

    series: [{
      data: [80, 85, 82, 90, 100, 98, 110, 105, 102, 112, 115, 125]
    }]
  });

}



